import PropTypes from 'prop-types';

// material-ui
import { Typography, ImageList, ImageListItem, ImageListItemBar, IconButton } from '@mui/material';
import { StarBorder as StarBorderIcon } from '@mui/icons-material';

//pure functions
function srcset(url, width, height, rows, cols) {
    let ext = /\.(\w{3,4})$/.exec(url)[0];
    return {
        src: `${url}_${width * cols}x${height * rows}${ext}`,
        srcSet: `${url}_${width * cols * 2}x${height * rows * 2}${ext} 2x`
    };
}

function PhotoGallery({ photoArr, photoSizing, photoCols, rowHeight, currentFolder, langPack }) {
    let { width, height } = photoSizing;
    return photoArr && photoArr.length ? (
        <ImageList cols={photoCols} rowHeight={rowHeight} sx={{ marginTop: '1px', marginBottom: '60px' }} variant="standard" gap={2}>
            {photoArr.map((item) => {
                return (
                    <ImageListItem key={item.url} cols={1} rows={1}>
                        <img {...srcset(item.url, width, height, 1, 1)} alt={item.file_name} loading="lazy" />
                        <ImageListItemBar
                            sx={{
                                background: 'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' + 'rgba(0,0,0,0.3) 50%, rgba(0,0,0,0) 100%)'
                            }}
                            title={item.file_name}
                            position="top"
                            actionIcon={
                                <IconButton sx={{ color: item.reference_count ? 'yellow' : 'white' }} aria-label={`star ${item.file_name}`}>
                                    <StarBorderIcon />
                                </IconButton>
                            }
                            actionPosition="left"
                        />
                    </ImageListItem>
                );
            })}
        </ImageList>
    ) : (
        <Typography variant="body2" sx={{ margin: '10px' }}>
            {currentFolder === langPack.folder.currentFolder ? langPack.folder.currentFolder : langPack.folder.dataNull}
        </Typography>
    );
}

PhotoGallery.propTypes = {
    photoArr: PropTypes.array,
    photoSizing: PropTypes.object,
    photoCols: PropTypes.number,
    rowHeight: PropTypes.number,
    currentFolder: PropTypes.string,
    langPack: PropTypes.object
};

export default PhotoGallery;
