import PropTypes from 'prop-types';

// material-ui
import { TablePagination } from '@mui/material';

function Pagination({ currentFolder, page, page_size, setPage, setPage_size, getPhotos, langPack }) {
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        let obj = { current_page: newPage + 1, page_size };
        if (currentFolder && currentFolder !== langPack.folder.currentFolder) obj.group_id = currentFolder;
        getPhotos(obj);
    };
    const handleChangeRowsPerPage = (event) => {
        let newSize = parseInt(event.target.value);
        setPage_size(newSize);
        let obj = { current_page: page + 1, page_size: newSize };
        if (currentFolder && currentFolder !== langPack.folder.currentFolder) obj.group_id = currentFolder;
        getPhotos(obj);
    };
    return (
        <TablePagination
            component="div"
            labelDisplayedRows={({ from, to }) => {
                return `${from}–${to}`;
            }}
            sx={{
                '& .MuiTablePagination-toolbar': { height: '29px', padding: '1px' }
            }}
            page={page}
            count={-1}
            onPageChange={handleChangePage}
            rowsPerPage={page_size}
            onRowsPerPageChange={handleChangeRowsPerPage}
        />
    );
}

Pagination.propTypes = {
    currentFolder: PropTypes.string,
    page: PropTypes.number,
    page_size: PropTypes.number,
    setPage: PropTypes.func,
    setPage_size: PropTypes.func,
    getPhotos: PropTypes.func,
    langPack: PropTypes.object
};

export default Pagination;
