import { useState } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Typography, FormGroup, FormControlLabel, Button, ButtonGroup, Input, Select, MenuItem, Breadcrumbs } from '@mui/material';
import {
    AddPhotoAlternate as AddPhotoAlternateIcon,
    Folder as FolderIcon,
    Check as CheckIcon,
    Cancel as CancelIcon,
    Add as AddIcon,
    ModeEdit as EditIcon,
    DeleteOutline as DeleteIcon,
    FileUpload as UploadIcon
} from '@mui/icons-material';

let newPhotoArr = [];
function PhotoFolder({ folderArr, currentFolder, setCurrentFolder, manageFolder, uploadPhotos, getPhotos, setPage, langPack }) {
    let [newFolder, setNewFolder] = useState(false);
    let [folderCommand, setFolderCommand] = useState(false);
    function onPhotoSelected(e) {
        let files = e.target.files;
        if (!files.length) {
            newPhotoArr = [];
            return;
        }
        newPhotoArr = files;
    }
    function refreshFolders() {
        setNewFolder('');
        setFolderCommand(false);
        return;
    }
    async function onFolderAdd() {
        let result = await manageFolder('add', { group_name: newFolder });
        if (result) {
            setCurrentFolder('');
            refreshFolders();
        }
        return;
    }
    async function onFolderRename() {
        let result = await manageFolder('rename', { group_id: currentFolder, group_name: newFolder });
        console.log(result);
        if (result) refreshFolders();
        return;
    }
    async function onFolderDelete() {
        manageFolder('delete', { group_id: currentFolder });
        setCurrentFolder('');
        refreshFolders();
        return;
    }
    async function onPhotoUpload() {
        for (let file of newPhotoArr) {
            await uploadPhotos(file);
        }
        newPhotoArr = [];
    }
    function changeFolder(e) {
        let value = e.target.value;
        if (value && value !== langPack.folder.currentFolder) {
            getPhotos({ group_id: value });
            setPage(0);
            setCurrentFolder(value);
        }
    }
    return !folderArr || !folderArr.length ? (
        <Typography variant="body2">{langPack.folder.folderNull}</Typography>
    ) : (
        <span style={{ marginTop: '20px' }}>
            {!folderCommand ? (
                <>
                    <Breadcrumbs aria-label="breadcrumb" sx={{ display: 'inline-block', marginRight: '5px' }}>
                        <FolderIcon />
                        <Select sx={{ height: '30px', minWidth: '100px' }} autoWidth={true} value={currentFolder} onChange={changeFolder}>
                            <MenuItem value={langPack.folder.currentFolder} disabled>
                                {langPack.folder.currentFolder}
                            </MenuItem>
                            {folderArr.map((obj, index) => (
                                <MenuItem key={index} value={obj.id}>
                                    {obj.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </Breadcrumbs>
                    <ButtonGroup variant="">
                        <Button onClick={() => setFolderCommand('add')}>
                            <AddIcon />
                        </Button>
                        <Button
                            onClick={() => {
                                if (!currentFolder || currentFolder === langPack.folder.currentFolder) return;
                                setFolderCommand('edit');
                                let thisFolder = folderArr.find((obj) => obj.id === currentFolder);
                                if (thisFolder) setNewFolder(thisFolder.name);
                            }}
                            disabled={!currentFolder || currentFolder === langPack.folder.currentFolder}
                        >
                            <EditIcon />
                        </Button>
                        <Button
                            onClick={() => setFolderCommand('delete')}
                            disabled={!currentFolder || currentFolder === langPack.folder.currentFolder}
                        >
                            <DeleteIcon />
                        </Button>
                        <Button
                            onClick={() => setFolderCommand('upload')}
                            disabled={!currentFolder || currentFolder === langPack.folder.currentFolder}
                        >
                            <UploadIcon />
                        </Button>
                    </ButtonGroup>
                </>
            ) : (
                <>
                    {folderCommand === 'add' ? (
                        <>
                            <Input
                                sx={{ height: '30px', marginRight: '10px' }}
                                variant="outlined"
                                color="secondary"
                                type="text"
                                placeholder={langPack.folder.addNewFolder}
                                onChange={(e) => setNewFolder(e.target.value.trim())}
                            />
                            <Button startIcon={<CheckIcon />} onClick={onFolderAdd}>
                                {langPack.common.yes}
                            </Button>
                            <Button startIcon={<CancelIcon />} onClick={() => setFolderCommand(false)}>
                                {langPack.common.no}
                            </Button>
                        </>
                    ) : folderCommand === 'edit' ? (
                        <>
                            <Input
                                sx={{ height: '30px', marginRight: '10px' }}
                                variant="outlined"
                                color="primary"
                                type="text"
                                defaultValue={folderArr.find((obj) => obj.id === currentFolder).name}
                                onChange={(e) => setNewFolder(e.target.value.trim())}
                            />
                            <Button startIcon={<CheckIcon />} onClick={onFolderRename}>
                                {langPack.common.yes}
                            </Button>
                            <Button startIcon={<CancelIcon />} onClick={() => setFolderCommand(false)}>
                                {langPack.common.no}
                            </Button>
                        </>
                    ) : folderCommand === 'delete' ? (
                        <>
                            <Button startIcon={<CheckIcon />} onClick={onFolderDelete}>
                                {langPack.common.yes}
                            </Button>
                            <Button startIcon={<CancelIcon />} onClick={() => setFolderCommand(false)}>
                                {langPack.common.no}
                            </Button>
                        </>
                    ) : folderCommand === 'upload' ? (
                        <>
                            <FormGroup>
                                <FormControlLabel
                                    sx={{ border: '1px dotted', height: '200px', margin: '20px', justifyContent: 'center' }}
                                    labelPlacement="top"
                                    control={<Input type="file" inputProps={{ multiple: true }} />}
                                    label={<AddPhotoAlternateIcon fontSize="large" />}
                                    onChange={onPhotoSelected}
                                />
                                <Button startIcon={<CheckIcon />} onClick={onPhotoUpload}>
                                    {langPack.common.yes}
                                </Button>
                                <Button startIcon={<CancelIcon />} onClick={() => setFolderCommand(false)}>
                                    {langPack.common.no}
                                </Button>
                            </FormGroup>
                        </>
                    ) : (
                        ''
                    )}
                </>
            )}
        </span>
    );
}

PhotoFolder.propTypes = {
    folderArr: PropTypes.array,
    currentFolder: PropTypes.string,
    setCurrentFolder: PropTypes.func,
    setPhotoArr: PropTypes.func,
    manageFolder: PropTypes.func,
    uploadPhotos: PropTypes.func,
    getPhotos: PropTypes.func,
    setPage: PropTypes.func,
    langPack: PropTypes.object
};

export default PhotoFolder;
